.input-textarea__label {
    display: flex;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    align-items: center;
}

.input-textarea__label--text {
    text-transform: uppercase;
    font-weight: 500;
    padding: 0 1rem;
    text-align: center;
    width: 20%;
    color: #999;
}

.input-textarea__input {
    border: 0;
    border-left: 1px solid #ccc;
    padding: 1rem 2rem;
    font-size: 3rem;
    outline: none;
    resize: none;
    width: 80%;
    height: 25vh;
}