.navbar {
    display: flex;
    justify-content: space-between;
    box-shadow: 0 .1rem 1rem rgba(0, 0, 0, 0.1);
}

.nav-logo-box {
    margin: 1.5rem;
    font-size: 2rem;
    font-weight: 700;
}


.navbar ul {
    list-style: none;
    display: flex;
}

.navbar ul li {
    margin-right: 3rem;
    padding: 2rem 3rem;
}

.navbar ul li a {
    padding: 0 .2rem;
    border-bottom: 2px solid transparent;
    transition: border-bottom .3s;
}

.navbar ul li a:hover {
    border-bottom: 2px solid teal;
}

nav.navbar .navbar-toggle {
    display: flex;
}

.navbar-menu {
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    padding: 2.5rem 2rem;
    cursor: pointer;
}

.navbar-menu__line {
    display: block;
    height: 2px;
    width: 18px;
    background-color: black;
    position: relative;
}

.navbar-menu__line::before {
    content: "";
    height: 100%;
    width: 100%;
    background-color: black;
    position: absolute;
    top: -5px;
}

.navbar-menu__line::after {
    content: "";
    height: 100%;
    width: 100%;
    background-color: black;
    position: absolute;
    top: 5px;
}

.navbar-menu__cross {
    background-color: transparent;
    transition: all 0.4s;
}

.navbar-menu__cross::before {
    top: 0;
    transform: rotate(45deg);
    transition: all 0.4s;
}

.navbar-menu__cross::after {
    top: 0;
    transform: rotate(-45deg);
    transition: all 0.4s;
}

@media only screen and (max-width: 791px) {
    .navbar, 
    .navbar ul {
        flex-direction: column;
    }

    nav.navbar .navbar-toggle {
        display: none;
    }

    .navbar ul li {
        text-align: center;
    }

    .navbar-menu {
        display: block;
    }
}