.search-container {
    width: 50%;
    margin: 5rem auto;
}

.homepage-hero {
    height: 50vh;
    background: linear-gradient(rgba(0, 0, 0, 0.315), rgba(0, 0, 0, 0.342)), url('https://images.unsplash.com/photo-1436775423025-9233051c49d4?ixlib=rb-1.2.1&auto=format&fit=crop&w=1267&q=80');
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.homepage-hero h1 {
    color: #fff;
    font-size: 15rem;
    font-weight: 100;
    letter-spacing: 2rem;
}

.search-container h3 {
    color: #777;
}

.search-container label {
    margin: 3rem 0;
}


@media only screen and (max-width: 1505px) {
    .homepage-hero h1 {
        font-size: 10rem;
    }
}



@media only screen and (max-width: 1200px) {
    .homepage-hero h1 {
        font-size: 7rem;
    }
}


@media only screen and (max-width: 850px) {
    .homepage-hero h1 {
        font-size: 5rem;
        width: 50%;
        line-height: 2;
    }

    .search-container {
        width: 90%;
        margin: 5rem auto;
    }
}