.search-results-hero {
    height: 50vh;
    background: linear-gradient(rgba(0, 0, 0, 0.473), rgba(0, 0, 0, 0.459)), url('https://images.unsplash.com/photo-1518159994930-3640550beffe?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1267&q=80');
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
}

.search-results-hero h3 {
    font-weight: 100;
    letter-spacing: 1rem;
    font-size: 10rem;
    text-transform: uppercase;
    color: #fff;
}

.search-results-container {
    padding: 3rem 2rem;
}

.search-results-container::after {
    content: "";
    display: table;
    clear: both;
}