.login-form {
    width: 50%;
    margin: 10rem auto;
}

.login-form label {
    margin-bottom: 3rem;
}

.login-form button:last-child {
    margin-left: 3rem;
    background-color: #777;
    color: #fff;
}


.login-form button:last-child:hover {
    background-color: #fff;
    color: #777;
}

@media only screen and (max-width: 850px) {
    .login-form {
        width: 90%;
        margin: 5rem auto;
    }

    .login-form label {
        font-size: .9rem;
    }

    .login-form__buttons {
        display: flex;
        flex-direction: column;
        width: 50%;
        margin-right: auto;
    }

    .login-form__buttons button:last-child {
        margin: 0;
        margin-top: 2rem;
    }
}



@media only screen and (max-width: 529px) {
    .login-form__buttons {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin: 0 auto;
    }
}