.input-text__label {
    display: flex;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    align-items: center;
}

.input-text__label--text {
    text-transform: uppercase;
    font-weight: 500;
    padding: 0 1rem;
    width: 20%;
    color: #999;
}

.input-text__input {
    border: 0;
    border-left: 1px solid #ccc;
    padding: 1rem 2rem;
    font-size: 3rem;
    outline: none;
    width: 80%;
}


@media only screen and (max-width: 850px) {
    .input-text__label,
    .input-text__input {
        font-size: 1.2rem;
    }
}