.search-form {
    width: 50%;
    margin: 5rem auto 0;
}


.search-form h3 {
    color: #777;
}


.search-form label {
    margin: 3rem 0;
}



@media only screen and (max-width: 850px) {
    .search-form {
        width: 90%;
        margin: 5rem auto;
    }

    .search-form label {
        font-size: .9rem;
    }
}