.signup-form {
    width: 50%;
    margin: 10rem auto;
}

.signup-form h3 {
    margin-bottom: 3rem;
}

.signup-form label {
    margin-bottom: 3rem;
}

.signup-form-error {
    margin: 1rem 0 2rem;
    color: red;
}


@media only screen and (max-width: 850px) {
    .signup-form {
        width: 90%;
        margin: 5rem auto;
    }

    .signup-form label {
        font-size: .9rem;
    }
}