.shop-card {
    padding: 3rem 6.5rem;
    border-radius: .5rem;
    box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    float: left;
    margin: 2rem;
}

.shop-card h3 {
    margin-bottom: 2rem;
}

.shop-card p {
    margin-bottom: .5rem;
    font-size: 1.5rem;
}

.shop-card__sub {
    display: flex;
    align-items: center;
    margin-top: 1rem;
    margin-bottom: .5rem;
}

.shop-card__sub > * {
    margin-right: 1rem;
}

.shop-card__queue {
    padding: 0 1rem;
    color: #fff;
    border-radius: 100px;
    margin-left: 1rem;
}

.shop-card__total-wait-time {
    padding: 0 1rem;
    color: #fff;
    border-radius: 100px;
    margin-left: 1rem;
}


.shop-card__green {
    background-color: rgb(11, 110, 11);
}


.shop-card__blue {
    background-color: rgb(9, 3, 56);
}


@media only screen and (max-width: 1035px) {
    .shop-card {
        width: 45%;
    }
}


@media only screen and (max-width: 857px) {
    .shop-card {
        width: 90%;
    }
}