.spinner-container {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.541);
}

.spinner {
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    border: 3px solid #ccc;
    border-right: 3px solid #333;
    animation: spin 0.5s linear infinite;
}


@keyframes spin {
    0% {
        transform: rotate(0deg);
    } 100% {
        transform: rotate(360deg);
    }
}